
import { isScrollBottom } from "@/utils/comm.js";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
export default {
components: {
XHeader
},
data(){
return {
record: [],
totalPage: 0,
page: 1,
upLoading: false,
upLoadingComplete: false,
upmore: true,
nick_name: ''
}
},
created(){
this.getRecord();
},
destroyed () {
window.onscroll = null;
},
mounted(){
this.$nextTick(()=>{
isScrollBottom(this.onReachBottom)
})
},
methods: {
async getRecord(){
let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
page: this.page
}
const [err, res] = await api.balancelog(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
if(this.page == 1){
this.nick_name = this.user.nick;
this.record = res.data.list;
this.totalPage = res.data.page_size;
}else{
let list = that.record;
list.push(...res.data.list);
}
this.showBottom()
}
},
showBottom(){
if (this.page < parseInt(this.totalPage)) {
this.upLoading = false;
this.upmore = true;
} else {
this.upLoadingComplete = true;
this.upLoading = false;         
this.upmore = false;
}
},
onReachBottom(){
if (this.upLoadingComplete) {
return
}
this.upmore = false;
this.upLoading = true;
let p = this.page
p++;
this.page = p;
this.getRecord()
}
},
computed: {
...mapGetters(["user", "account", "token", "carpayInfo"])
},
}



